import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Tabbar from '@vkontakte/vkui/dist/components/Tabbar/Tabbar';
import TabbarItem from '@vkontakte/vkui/dist/components/TabbarItem/TabbarItem';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Icon28HelpOutline from '@vkontakte/icons/dist/28/help_outline';
import Icon28ArticleOutline from '@vkontakte/icons/dist/28/article_outline';
import {platform, IOS, HeaderButton} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon16Fire from '@vkontakte/icons/dist/16/fire';
import Icon28FavoriteOutline from '@vkontakte/icons/dist/28/favorite_outline';

const Persik4 = ({go, id, fetchedUser}) => (


<Panel id={id}>
		<PanelHeader>Новости</PanelHeader>
    <Tabbar>
          <TabbarItem
            onClick={go}
            data-to="home"
          ><Icon28ArticleOutline /></TabbarItem>
          <TabbarItem
            onClick={go}
            data-to="persik5"
            label="new"
          ><Icon28FavoriteOutline /></TabbarItem>
          <TabbarItem
            onClick={go}
            data-to="persik4"
          ><Icon16Fire width={30} height={30} fill="#4682B4"/></TabbarItem>
          <TabbarItem
            onClick={go}
            data-to="persik"
          ><Icon28HelpOutline /></TabbarItem>
        </Tabbar>
        
        <Group title="О разделе">
          <Div>
          <i>В этот раздел будут выкладываться все <b>ВАЖНЫЕ</b> новости жизни экспертов и беседы.</i>
          </Div>
        </Group>
        <Group>
          <Div>
          <div style={{ display: 'flex' }}>
        <b>Инфа по ачивкам</b><h> </h><div className="Cell__description">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; 17/19.01.2020</div>
        
        </div>
        </Div>
        <Div>
        <i>В ближайщее время ачивки выдаваться НЕ БУДУТ!!!</i>
        </Div>
		 <br/>
		 <br/>
          
        </Group>
        
	</Panel>
);



Persik4.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
    fetchedUser: PropTypes.shape({
    photo_200: PropTypes.string,
    first_name: PropTypes.string,
    last_name: PropTypes.string,
    city: PropTypes.shape({
      title: PropTypes.string,
    }),
  }),

};

export default Persik4;